.button-header {
  background-color: forestgreen;
  color: white;
  border: 1px solid forestgreen;
  border-radius: 30px;
  padding: 10px;

}

.button-header:hover {
  background-color: darkgreen;
}

.button-header:link, .button-header:visited {
  color: white;
  text-decoration: none;
  cursor: auto;
}

.button-header:link:active, .button-header:visited:active {
  color: white;
  text-decoration: none;
}

.header-header {
  color: forestgreen;
  font-weight: bold;
}

.info {
  text-align: center;
  margin: auto;
}

.info-header {
  font-size: 150%;
  /*max-width: 600px;*/

}

.header-text {
