.read-info-link {
  font-size: 150%;
}

.read-info-link:link, .read-info-link:visited {
  color: forestgreen;
  text-decoration: underline;
  cursor: auto;
}

.read-info-link:link:active, .read-info-link:visited:active {
  color: forestgreen;
  text-decoration: underline;
}

.footer {
