.App {
  text-align: center;
  font-family: Amiko;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.loader {
  -webkit-animation: loader-spin infinite 5s linear;
          animation: loader-spin infinite 5s linear;
  position: fixed;
  left: 50%;
  top: 50%;
}

.component {
  text-align: center;
}

.navbar-brand {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  /*cursor: pointer;*/
}
.navbar-brand>img {
  height: 150px;
  width: 150px;
  /*padding: 0px 5px;*/
  /*margin-top: 20px;*/
}

.top {
  padding: 20px;
}

.fonts {
  font-family: Amiko;
  color: forestgreen;
}

@-webkit-keyframes loader-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes loader-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}





